import env from '../../env'
//
export async function searchLiveRoom(keyword,page){
    
    return new Promise((success,error)=>{
        $.ajax({
            type : 'GET',
            url  : env.host_self+'/douyu/'+keyword+'?page='+page,
            success : function(response){
                success(response)
            },
            error : function(xhr){
                error('')
            }
        });
    })
}


